Template.result.helpers({
  showBack:function(){
    return false;
  },
  showClose:function(){
    return false;
  },
  title:function(){
    var titles = {
      ticket:Meteor.settings.public.tenant_livechat_settings.ticketTitle,
      livechat:Meteor.settings.public.tenant_livechat_settings.chatTitle
    }
    return titles[FlowRouter.current().params.type];
  },
  status:function(){
    if(FlowRouter.current().queryParams.status == 'true'){
      return 'icon-check';
    }else{
      return 'icon-close';
    }
  },
  introMsg:function(){
    var current = FlowRouter.current();
    return settings.introMsg[current.params.type][current.queryParams.status];
  }
});
Template.result.events({
  'click .btn-confirm':function(event){
    event.preventDefault();
    Session.set("hasSendMsg", false);
    outerAgent.closeWindow();
    visitor.ticketOrLivechat();
  }
});
